'use client';
import { useState, useEffect } from 'react';
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from '@/components/ui/card';
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from '@/components/ui/table';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { DollarSign, Users, Star, TrendingUp, TrendingDown, RefreshCw } from 'lucide-react';
import { useRouter } from 'next/navigation';
import { Button } from '@/components/ui/button';
import { useToast } from '@/hooks/use-toast';
import { apiService } from '@/lib/api';
import { InsightsCalculator } from '@/lib/insights-calculations';

interface DashboardData {
  kpis: {
    total_chatters: number;
    total_models: number;
    total_clients: number;
  };
  snapshot: {
    revenue: number;
    real_revenue: number;
    cost: number;
    profit: number;
    real_profit: number;
  };
  month_kpis?: {
    period_breakdown: {
      period_1: { revenue: number; profit: number };
      period_2: { revenue: number; profit: number };
    };
    growth_metrics: {
      revenue_growth_pct: number;
      profit_growth_pct: number;
    };
  };
}

interface LeaderboardData {
  top_model_by_revenue: Array<{ id: string; value: number }>;
  top_model_by_profit: Array<{ id: string; value: number }>;
  most_expensive_chatter: Array<{ id: string; value: number }>;
}

export default function DashboardPage() {
  const { toast } = useToast();
  const [selectedMonth, setSelectedMonth] = useState('current');
  const [isRefreshing, setIsRefreshing] = useState(false);
  const [loading, setLoading] = useState(true);
  const [dashboardData, setDashboardData] = useState<DashboardData | null>(null);
  const [leaderboardData, setLeaderboardData] = useState<LeaderboardData | null>(null);
  const [models, setModels] = useState<any[]>([]);
  const [chatters, setChatters] = useState<any[]>([]);

  const currentYear = new Date().getFullYear();
  const currentMonth = new Date().toLocaleString('default', { month: 'long' });
  const previousMonth = new Date(Date.now() - 30 * 24 * 60 * 60 * 1000).toLocaleString('default', { month: 'long' });

  const loadDashboardData = async () => {
    try {
      setLoading(true);
      const period = selectedMonth === 'current' ? currentMonth : previousMonth;
      
      // Load dashboard KPIs and snapshot
      const [kpisResponse, leaderboardResponse, modelsResponse, chattersResponse] = await Promise.all([
        apiService.getDashboardKPIs(period, currentYear),
        apiService.getLeaderboards(period, currentYear),
        apiService.getAllModels({ status_filter: 'Active' }),
        apiService.getAllChatters({ status_filter: 'active' })
      ]);

      if (kpisResponse.status === 'Success' && kpisResponse.data) {
        setDashboardData(kpisResponse.data);
      }

      if (leaderboardResponse.status === 'Success' && leaderboardResponse.data) {
        setLeaderboardData(leaderboardResponse.data);
      }

      if (modelsResponse.status === 'Success' && modelsResponse.data) {
        setModels(modelsResponse.data);
      }

      if (chattersResponse.status === 'Success' && chattersResponse.data) {
        setChatters(chattersResponse.data);
      }
    } catch (error) {
      console.error('Error loading dashboard data:', error);
      toast({
        title: "Error",
        description: "Failed to load dashboard data. Please try again.",
        variant: "destructive",
      });
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    loadDashboardData();
  }, [selectedMonth]);

  const handleRefresh = async () => {
    setIsRefreshing(true);
    await loadDashboardData();
    setIsRefreshing(false);
  };

  // Calculate unique clients from models
  const uniqueClients = models.length > 0 
    ? [...new Set(models.map(model => model.clientAgencyName))].length 
    : 0;

  // Prepare KPIs data
  const kpis = [
    {
      icon: <Users className="h-6 w-6 text-muted-foreground" />,
      title: 'Total Chatters',
      value: chatters.length.toString(),
      change: 'Active chatters only',
    },
    {
      icon: <Star className="h-6 w-6 text-muted-foreground" />,
      title: 'Total Models',
      value: models.length.toString(),
      change: 'Active models only',
    },
    {
      icon: <DollarSign className="h-6 w-6 text-muted-foreground" />,
      title: 'Total Clients',
      value: uniqueClients.toString(),
      change: 'Unique client agencies',
    },
  ];

  // Prepare weekly snapshot data
  const weeklySnapshot = dashboardData?.month_kpis ? [
    {
      week: 'Period 1',
      revenue: dashboardData.month_kpis.period_breakdown.period_1.revenue,
      costs: dashboardData.snapshot.cost - dashboardData.month_kpis.period_breakdown.period_2.profit,
      profit: dashboardData.month_kpis.period_breakdown.period_1.profit,
      change: `${dashboardData.month_kpis.growth_metrics.revenue_growth_pct >= 0 ? '+' : ''}${dashboardData.month_kpis.growth_metrics.revenue_growth_pct.toFixed(1)}%`
    },
    {
      week: 'Period 2',
      revenue: dashboardData.month_kpis.period_breakdown.period_2.revenue,
      costs: dashboardData.snapshot.cost - dashboardData.month_kpis.period_breakdown.period_1.profit,
      profit: dashboardData.month_kpis.period_breakdown.period_2.profit,
      change: `${dashboardData.month_kpis.growth_metrics.profit_growth_pct >= 0 ? '+' : ''}${dashboardData.month_kpis.growth_metrics.profit_growth_pct.toFixed(1)}%`
    }
  ] : [];

  const totalRevenue = dashboardData?.snapshot.revenue || 0;
  const totalCosts = dashboardData?.snapshot.cost || 0;
  const totalProfit = dashboardData?.snapshot.profit || 0;

  if (loading) {
    return (
      <div className="flex flex-col gap-6">
        <header className="flex items-center justify-between">
          <div>
            <h1 className="text-3xl font-bold font-headline">Dashboard</h1>
            <p className="text-muted-foreground">
              Loading your finance overview...
            </p>
          </div>
        </header>
        <div className="flex items-center justify-center py-12">
          <div className="flex items-center space-x-2">
            <RefreshCw className="h-6 w-6 animate-spin" />
            <span>Loading dashboard data...</span>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="flex flex-col gap-6">
      <header className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold font-headline">Dashboard</h1>
          <p className="text-muted-foreground">
            Welcome back! Here's your finance overview for {selectedMonth === 'current' ? currentMonth : previousMonth}.
          </p>
        </div>
        <div className="flex items-center gap-2">
          <Select value={selectedMonth} onValueChange={setSelectedMonth}>
            <SelectTrigger className="w-40">
              <SelectValue />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="current">Current Month</SelectItem>
              <SelectItem value="previous">Previous Month</SelectItem>
            </SelectContent>
          </Select>
          <Button
            variant="outline"
            size="icon"
            onClick={handleRefresh}
            disabled={isRefreshing}
          >
            <RefreshCw className={`h-4 w-4 ${isRefreshing ? 'animate-spin' : ''}`} />
          </Button>
        </div>
      </header>

      <section className="grid grid-cols-1 gap-4 md:grid-cols-2 lg:grid-cols-3">
        {kpis.map((kpi) => (
          <Card key={kpi.title}>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium">
                {kpi.title}
              </CardTitle>
              {kpi.icon}
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{kpi.value}</div>
              <p className="text-xs text-muted-foreground">{kpi.change}</p>
            </CardContent>
          </Card>
        ))}
      </section>

      <section className="grid grid-cols-1 gap-4 md:grid-cols-3">
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Monthly Revenue</CardTitle>
            <TrendingUp className="h-4 w-4 text-green-600" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">${totalRevenue.toLocaleString()}</div>
            <p className="text-xs text-muted-foreground">
              +12% from last month
            </p>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Monthly Costs</CardTitle>
            <TrendingDown className="h-4 w-4 text-red-600" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">${totalCosts.toLocaleString()}</div>
            <p className="text-xs text-muted-foreground">
              +8% from last month
            </p>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Monthly Profit</CardTitle>
            <TrendingUp className="h-4 w-4 text-green-600" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">${totalProfit.toLocaleString()}</div>
            <p className="text-xs text-muted-foreground">
              +15% from last month
            </p>
          </CardContent>
        </Card>
      </section>

      <section>
        <Card>
          <CardHeader>
            <CardTitle>Period Snapshot</CardTitle>
            <CardDescription>
              Performance breakdown for {selectedMonth === 'current' ? currentMonth : previousMonth}.
            </CardDescription>
          </CardHeader>
          <CardContent>
            {weeklySnapshot.length > 0 ? (
              <Table>
                <TableHeader>
                  <TableRow>
                    <TableHead>Period</TableHead>
                    <TableHead className="text-right">Revenue</TableHead>
                    <TableHead className="text-right">Costs</TableHead>
                    <TableHead className="text-right">Profit</TableHead>
                    <TableHead className="text-right">Growth</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {weeklySnapshot.map((item) => (
                    <TableRow key={item.week}>
                      <TableCell>{item.week}</TableCell>
                      <TableCell className="text-right text-green-500">
                        ${item.revenue.toLocaleString()}
                      </TableCell>
                      <TableCell className="text-right text-red-500">
                        ${item.costs.toLocaleString()}
                      </TableCell>
                      <TableCell className="text-right font-bold">
                        ${item.profit.toLocaleString()}
                      </TableCell>
                      <TableCell className="text-right">
                        <span className={`text-xs ${item.change.startsWith('+') ? 'text-green-600' : 'text-red-600'}`}>
                          {item.change}
                        </span>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            ) : (
              <div className="text-center py-8 text-muted-foreground">
                No period data available for {selectedMonth === 'current' ? currentMonth : previousMonth}.
              </div>
            )}
          </CardContent>
        </Card>
      </section>

      <section className="grid grid-cols-1 gap-6 lg:grid-cols-3">
        <LeaderboardCard 
          title="Top Model by Revenue" 
          data={leaderboardData?.top_model_by_revenue || []} 
          models={models}
          valueKey="value" 
          valueHeader="Revenue" 
          linkPrefix='/dashboard/models' 
          linkKey='id' 
        />
        <LeaderboardCard 
          title="Top Model by Profit" 
          data={leaderboardData?.top_model_by_profit || []} 
          models={models}
          valueKey="value" 
          valueHeader="Profit" 
          linkPrefix='/dashboard/models' 
          linkKey='id' 
        />
        <LeaderboardCard 
          title="Most Expensive Chatter" 
          data={leaderboardData?.most_expensive_chatter || []} 
          chatters={chatters}
          valueKey="value" 
          valueHeader="Pay" 
          linkPrefix='/dashboard/chatters' 
        />
      </section>
    </div>
  );
}

interface LeaderboardCardProps {
    title: string;
    data: Array<{ id: string; value: number }>;
    models?: any[];
    chatters?: any[];
    valueKey: string;
    valueHeader: string;
    linkPrefix: string;
    linkKey?: string;
}

function LeaderboardCard({ title, data, models = [], chatters = [], valueKey, valueHeader, linkPrefix, linkKey }: LeaderboardCardProps) {
  const router = useRouter();

  const handleRowClick = (item: any) => {
      if (linkKey) {
          router.push(`${linkPrefix}/${item[linkKey]}/insights`);
      } else {
          router.push(linkPrefix);
      }
  }

  const getEntityName = (id: string) => {
    // Try to find in models first
    const model = models.find(m => m.model_id === id);
    if (model) return model.modelName;
    
    // Then try chatters
    const chatter = chatters.find(c => c.chatter_id === id);
    if (chatter) return chatter.name;
    
    return `Entity ${id}`;
  }

  const formatValue = (value: number) => {
    if (valueKey === 'value') {
      return `$${value.toLocaleString()}`;
    }
    return value.toString();
  }

  return (
    <Card>
      <CardHeader>
        <CardTitle>{title}</CardTitle>
      </CardHeader>
      <CardContent>
        {data.length > 0 ? (
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead>Rank</TableHead>
                <TableHead>Name</TableHead>
                <TableHead className="text-right">{valueHeader}</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {data.map((item, index) => (
                <TableRow key={item.id}>
                  <TableCell>{index + 1}</TableCell>
                  <TableCell>
                      <Button variant='link' className='p-0 h-auto' onClick={() => handleRowClick(item)}>
                          {getEntityName(item.id)}
                      </Button>
                  </TableCell>
                  <TableCell className="text-right">{formatValue(item.value)}</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        ) : (
          <div className="text-center py-8 text-muted-foreground">
            No data available
          </div>
        )}
      </CardContent>
    </Card>
  );
} 